@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Rubik:wght@400;500;600;700&display=swap');

*{
    margin: 0;
    padding: 0;
}
body {
    font-family: 'Rubik', sans-serif;
}

/* NAV DESKTOP */
nav{
    width: 100%;
    height: 5rem;
    background-color: #fff;
    box-shadow: 0px 5px 15px rgba(158, 160, 161, 0.2);
    position: fixed;
    top: 0;

}
nav h1{
    font-family: 'Montserrat', sans-serif;
    font-size: 1.25rem;
    letter-spacing: 0.18em;
    display: inline;
    margin: 1.5rem 0 0 5rem;
    position: absolute;
}
nav ul{
    display: inline;
    float: right;
    margin: 1.5rem 5rem 0 0;
}
nav ul li{
    display: inline;
    margin-left: 2rem;
}
nav ul li a{
    text-decoration: none;
    color: black;
    font-weight: 500;
}
nav ul li a.register{
    color: #0177F9;
    padding: 10px 20px;
    border: 2px solid #0177F9;
    border-radius: 10px;
}
nav ul li a:hover{
    text-decoration: underline;
    color: #0177F9;
}



/* HERO DESKTOP */
#hero{
    width: 100%;
    display: flex;
    flex-flow: row nowrap;
    margin-top: 5rem;
}
#hero .left{
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
}
#hero .left .text{
    width: 80%;
}
#hero .left .text h1{
    font-size: 2.5rem;
    line-height: 3.5rem;
    margin-bottom: 1rem;
}
#hero .left .text p{
    font-size: 1.1rem;
    line-height: 1.5rem;
    margin-bottom: 2rem;
}
#hero .left .text a{
    text-decoration: none;
    padding: 10px 20px;
    background-color: #0177F9;
    color: #fff;
    font-weight: 500;
    border-radius: 10px;
}
#hero .left .text a:hover{
    background-color: #97CEE8;
    color: #0177F9;
}
#hero .right img{
    width: 90%;
    height: 90%;
}
#hero .right{
    width: 50%;
    display: flex;
    justify-content: center;
    flex-flow: column wrap;
}
#hero .right #box{
    width: 80%;
    box-shadow: 0px 4px 12px rgba(55, 55, 55, 0.25);
    border-radius: 10px;    
    background-color: #fff;
    display: flex;
    justify-content: center;
    margin-top: -5rem;
    margin-left: 3rem;
}
#hero .right #box .stats{
    width: 29%;
    margin: 1.5rem;
    text-align: center;
}
#hero .right #box .stats h1{
    color: #0177F9;
    margin-bottom: 0.5rem;
    font-weight: 600;
    font-size: 1.75rem;
}
#hero .right #box .stats p{
    color: #A1A1A1;
    font-size: 1.1rem;
}


/* WHY CHOOSE DESKTOP */
#why-choose{
    display: flex;
    margin-top: 5rem;
}
#why-choose .left{
    width: 50%;
}
#why-choose .left img{
    border-radius: 150px 10px 10px 10px;
    margin-left: 4rem;
}
#why-choose .right{
    width: 50%;
    margin-top: 2rem;
}
#why-choose .right h1{
    font-size: 2.5rem;
    margin-bottom: 1rem;
}
#why-choose .right p{
    font-size: 1.1rem;
    line-height: 1.5rem;
    width: 70%;
}
#why-choose aside{
    margin-top: 2rem;
}
#why-choose .box{
    display: flex;
    align-items: center;
    margin-top: 1.5rem;
}
#why-choose .box p{
    margin-left: 1rem;
}


#our-best{
    width: 100%;
    margin-top: 5rem;
}
#our-best h1{
    text-align: center;
    font-size: 2.5rem;
    margin-bottom: 2rem;
}
#our-best aside{
    width: 100%;
    display: flex;
    justify-content: center;
}
#our-best aside .box{
    width: 20%;
    background: #FFFFFF;
    box-shadow: 0px 8px 22px rgba(77, 77, 77, 0.1);
    border-radius: 10px;
    margin: 1.5rem;
    display: flex;
    flex-flow: column wrap;
    align-items: center;
}
#our-best aside .box #pic{
    margin: 1.5rem 0 1rem 0;
}
#our-best aside .box h3{
    margin-bottom: 0.5rem;
    font-size: 1.25rem;
}
#our-best aside .box #blur{
    color: #D6D6D6;
    font-size: 1rem;
}
#our-best aside .box .stars{
    width: 50%;  
    margin-bottom: 1rem;
}
#our-best aside .box p{
    margin-bottom: 1rem;
}
#our-best aside .box:hover{
    background-color: #0177F9;
    color: #fff;
}
#our-best .fa-regular, .fa-solid{
    color: #0177F9;
}
#our-best aside .box:hover .fa-regular, #our-best aside .box:hover .fa-solid{
    color: #fff;
}

#location{
    width: 100%;
    display: flex;
    margin-top: 3rem;
    margin-bottom: 5rem;
}
#location .left{
    width: 50%;
}
#location .left .text{
    margin: 5rem;
}
#location .left .text h1{
    font-size: 2rem;
    margin-bottom: 1rem;
}
#location .left .text h2{
    font-size: 1.25rem;
    margin-bottom: 1rem;
}
#location .left .text p{
    font-size: 1rem;
    margin-bottom: 0.5rem;
}
#location .right{
    width: 50%;
}
#location .right img{
    width: 90%;
}




























/* FOOTER DESKTOP */
footer{
    width: 100%;
    height: auto;
    background-color: #F3F3FB;
    font-family: 'Inter', sans-serif;
}
footer article{
    padding: 2.5rem;
    display: flex;
}
footer article .logo{
    width: 20%;
}
footer article .logo h1{
    font-family: 'Montserrat', sans-serif;
    font-size: 1.25rem;
    letter-spacing: 0.18em;
    margin-top: 1rem;
}
footer article .navigation{
    width: 60%;
}
footer article .navigation h1{
    font-weight: 600;
    font-size: 1.25rem;
    color: #232323;
    margin-bottom: 1.5rem;
}
footer article .navigation .nav-boxes{
    display: flex;
}
footer article .navigation .nav-boxes .box{
    width: 30%;
}
footer article .navigation .nav-boxes .box ul li{
    list-style: none;
}
footer article .navigation .nav-boxes .box ul li a{
    text-decoration: none;
    font-weight: 400;
    font-size: 1.0625rem;
    line-height: 3rem;
    color: #232323;

}
footer article .contacts h1{
    font-weight: 600;
    font-size: 1.25rem;
    color: #232323;
    margin-bottom: 1.5rem;
}
footer article .contacts .contact-us{
    line-height: 2rem;
}
footer article .contacts .contact-us .icon{
    margin-right: 1rem;
}
footer article .contacts .contact-us .icon .fa{
    color: #0177F9;
}
footer article .contacts .socials{
    margin-top: 1rem;
}
footer article .contacts .fa-brand{
    color: #fff;
    margin-right: 0.8rem;
    padding: 8px;
    font-size: 20px;
    background-color: #97CEE8;
}
footer article .contacts .fa-brand:hover{
    background-color: #0177F9;
}
footer .credits{
    width: 100%;
    padding-bottom: 1rem;
    display: flex;
    justify-content: space-around;
}
footer .credits span{
    font-weight: 400;
    font-size: 1rem;
    color: #A1A1A1;
    opacity: 0.6;
}















/* MOBILE ///////////////////////////////////////////////////////////////////////////////////////////////////////// */
@media screen and (max-width: 1000px) {
    
    footer article{
        padding: 2.5rem;
        flex-flow: column wrap;
    }
    footer article .logo h1{
        margin-bottom: 2rem;
    }
    footer article .navigation{
        width: 100%;
    }
    footer article .navigation .nav-boxes{
        width: 100%;
        flex-flow: row wrap;
    }
    footer article .navigation .nav-boxes .box{
        width: 40%;
        margin-bottom: 1rem;
    }
    footer article .contacts h1{
        margin-top: 1rem;
    }
    footer .credits{
        flex-flow: column wrap;
        justify-content: center;
        align-items: center;
    }
    footer .credits span{
        margin-bottom: 1rem;
    }
}